.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 2% 5%;
}

.item {
  border: 1px solid rgba(153, 153, 153, 0.3);
  padding: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #e1e1e1;
}

.item:nth-child(1) {
  grid-column: 1/5;
  background: #838393;
  justify-content: flex-end;
  color: white;
}

.item:nth-child(18) {
  grid-column: 1/3;
}

.item:nth-child(5),
.item:nth-child(9),
.item:nth-child(13),
.item:nth-child(17),
.item:nth-child(20) {
  background: #f4933c;
}

button {
  cursor: pointer;
}

button:hover {
  opacity: 0.7;
}

button:active {
  opacity: 0.4;
}

.result {
  display: flex;

  /* justify-content: space-evenly; */
  align-items: center;
  margin: 10px;
}

.equal {
  color: white;
  font-size: 25px;
  font-weight: bold;
}

.total {
  background: #f4933c;

  /* padding: 15px; */
  border-radius: 40px;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  margin-left: 20px;
}
